

    import Cronometro from './Cronometro.vue';
    import ITarefa from '../interfaces/ITarefa';
    import { defineComponent , PropType } from 'vue';
    import Box from './Box.vue';

    export default defineComponent({
        name:'MinhasTarefas',
        components:{
            Cronometro,
            Box
        },
        props:{
            tarefa:{
                type: Object as PropType<ITarefa>,
                required : true
            }
        }
    })

